.background-popup-options {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
}

.container-options {

    .btn-options {
        color: #fff;
        font-size: 23px;
        align-self: center;
        text-shadow: 0 0 3px black;
        cursor: pointer;
        z-index: 2;
        position: absolute;
        right: 200px;
        top: 30px;
        width: 85px;
        height: 25px;

        .popup-options {
            cursor: default;
            position: relative;
            width: 560px;
            height: 500px;
            background-color: #f2f3f4;
            border: 1px solid black;
            top: 10px;
            left: 150px;
            float: right;
            z-index: 2;
            box-shadow: 0 0 7px black;
            display: none;
            flex-flow: row wrap;

            .tab-option {
                font-weight: bold;
                text-shadow: none;
                color: black;
                width: auto;
                height: 28px;
                padding: 5px 10px;
                font-size: 20px;
                background-color: #d3d3d3;
                cursor: pointer;
                box-sizing: content-box;

                .tab {
                    cursor: default;
                    width: 100%;
                    height: 92%;
                    background-color: white;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    box-shadow: 0 0 2px 0px black;
                    flex-flow: column;
                    display: none;

                }

                #tab-background.tab {

                    #image-selector {
                        display: flex;
                        flex-flow: row wrap;
                        overflow-y: auto;
                        height: 80%;
                        width: 97%;
                        margin: 0 auto;
                        background-color: #f0f8ff;

                        >.ui.active.transition.visible.dimmer {
                            padding: 0;
                            width: inherit !important;
                            height: inherit !important;
                        }

                        >* {
                            display: flex;
                            flex-flow: row nowrap;
                            width: 105px;
                            height: 105px;

                            .folder-name {
                                display: flex;
                                flex-flow: column;
                                width: 90px;
                                height: 90px;
                                margin: 0 7px 30px 6px;
                                text-align: center;

                                .folder-img {
                                    width: inherit;
                                    height: inherit;
                                    cursor: pointer;
                                }
                            }

                            .img-background-border {
                                width: 90px;
                                height: 90px;
                                margin: 5px;
                                border: 2px dashed #000;
                                border-radius: 100px;
                                cursor: pointer;
                                position: relative;

                                .img-background {
                                    object-fit: cover;
                                    height: 90px;
                                    border-radius: 100px;
                                    margin: auto;
                                }

                                .bg-img-active {
                                    width: 88px;
                                    height: 88px;
                                }
                            }

                            .bg-img-border-active {
                                border: 4px dashed #f00 !important;
                                width: 88px;
                                height: 88px;
                            }

                            label[for="file-to-push"] {
                                margin: auto;
                                cursor: pointer;
                                border-radius: 200px;
                                transition: .3s;

                                i {
                                    height: 1em !important;
                                    font-size: 2.1em;
                                    box-shadow: 0 0 4px 5px #f2711c;
                                    border: 2px solid #f08137;
                                }
                            }

                            label[for="file-to-push"]:hover {
                                background-color: #ebebeb;
                            }
                        }
                    }

                    .box-favorite {
                        margin-left: -20px;
                        margin-top: 4px;
                        color: #f00;
                    }

                    .image-options {
                        display: flex;
                        flex-flow: column;
                        margin: auto;

                        p.text-info {
                            width: 100%;
                            margin: 0 0 15px 0;
                            padding: 0;
                            font-size: 17px;
                            text-align: center;
                        }

                        div.options {
                            margin: auto;
                            display: flex;
                            flex-flow: row;

                            .ui.checkbox {
                                cursor: pointer;
                                font-size: 17px;
                                margin-right: 10px;

                                label {
                                    color: black;
                                    padding-left: 1.3em;
                                }

                                .box:after,
                                label:after {
                                    font-size: 18px;
                                    width: 19px;
                                    height: 19px;
                                }
                            }
                        }
                    }
                }

                #tab-favorites.tab {
                    overflow: auto;

                    >.accordion {
                        background: #9fceff6e;

                        .title {
                            color: #0000009e;
                            border-top: 1px solid;
                        }

                        .title.active,
                        .title:hover {
                            color: #000000f2 !important;
                        }

                        .content {
                            position: relative;
                            padding: .5em 1em;

                            .edit-group {
                                position: absolute;
                                top: -35px;
                                right: 18px;
                                margin: 0;
                                padding: .6em;

                                i {
                                    margin-right: -2px !important;
                                    font-size: 15px;
                                }
                            }

                            .ui.tag.label {
                                margin: 3px .5em 3px 1em;
                            }

                            .new-item-tag>button.new-item {
                                width: -webkit-fill-available;
                                width: -moz-available;
                                margin: 1em 0 0 0;
                            }

                        }

                        .new-link-fav {
                            width: -webkit-fill-available;
                            width: -moz-available;
                            position: relative;
                            padding: 10px !important;
                            margin: 0;
                        }

                    }

                    button.new-group {
                        border-radius: unset;
                        margin: 0;
                    }


                }

                #tab-settings.tab {
                    overflow: hidden auto;
                    background: #cce7ff;

                    .ui.card.card-settings {
                        width: 95%;
                        margin: 5px auto;
                        min-height: auto;

                        .col {
                            width: 100%;
                        }

                        .content {
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            color: #000;

                            .right {
                                margin-left: auto;

                                >label {
                                    cursor: pointer;
                                }

                                .ui.toggle.checkbox {

                                    label {
                                        font-size: 1.37em !important;
                                        padding-left: 3em;
                                        color: #000 !important;
                                    }

                                    input~.box:after,
                                    input~label:after {
                                        left: 0px;
                                    }

                                    input:checked~.box:after,
                                    input:checked~label:after {
                                        left: 2rem;
                                    }

                                    .box:before,
                                    label:before {
                                        box-shadow: 0 0 2px 1px #000;
                                        background: #0000007a;
                                    }
                                }

                            }

                            .ui.dropdown {
                                width: 30% !important;
                                margin-left: auto;
                                padding: .5em;
                                border: 1px solid #7b7b7b;
                                border-radius: 20px;
                                min-height: 1em;

                                .menu {
                                    border-color: #7b7b7b;
                                    max-height: 16rem;
                                }

                                .delete.icon,
                                .dropdown.icon,
                                .search.icon {
                                    padding: 0.7em;
                                }
                            }

                            .ui.search.selection.dropdown>input.search {
                                padding: .4em .6em;
                                width: 7em;
                            }

                            .option-card-settings {
                                margin: 5px 0;

                                .content {
                                    padding: 0.5em 1em;
                                    background-color: #cccccc;
                                    position: relative;

                                    .bg-click {
                                        display: none;
                                        position: fixed;
                                        top: 0;
                                        right: 0;
                                        bottom: 0;
                                        left: 0;
                                        background-color: #00000000;
                                        z-index: 99999;
                                    }

                                    .color-text {
                                        border: 1px solid #848484;
                                        border-radius: 5px;
                                        padding: .3em .6em;
                                        height: fit-content;
                                        background-color: #f3f3f3;
                                        box-shadow: 0 0 8px 1px inset #8c8c8c;
                                        width: 150px;
                                        text-align: center;
                                        position: absolute;
                                        top: 4px;
                                        right: 4px;
                                        cursor: pointer;
                                        z-index: 100000;
                                    }

                                    .chrome-picker {
                                        display: block !important;
                                        position: absolute;
                                        right: 4px;
                                        top: 46px;
                                        z-index: 100001;
                                    }

                                    .chrome-picker.dis-none {
                                        display: none !important;
                                    }
                                }
                            }

                            .option-card-settings.paypal-options {

                                .ui.radio.checkbox {
                                    margin: auto 0;

                                    label {
                                        font-size: 1.2em;
                                        padding-left: 1.2em;
                                    }

                                    .box:after,
                                    label:after {
                                        top: -2px;
                                        left: -3px;
                                        width: 23px;
                                        height: 23px;
                                    }
                                }

                                .ui.input {
                                    font-size: .8em;
                                    margin-left: unset;
                                }
                            }
                        }
                    }
                }

                .tab-active {
                    display: flex;
                }
            }

            .tab-option-active {
                color: #5692F5;
                background-color: white;
                border-bottom: 2px solid;
            }

            #favorites {
                margin: 0 5px;
            }

        }

        .triangle {
            cursor: default;
            position: relative;
            background-color: #f2f3f4;
            width: 14px;
            height: 12px;
            left: 35px;
            top: 5px;
            border: 1px solid black;
            transform: rotate(45deg);
            z-index: 2;
            display: none;

            .square {
                position: relative;
                width: 26px;
                height: 10px;
                top: 0;
                background-color: #f2f3f4;
                transform: rotate(-45deg);
            }
        }
    }

    .date-time {
        color: #fff;
        position: absolute;
        top: 10px;
        right: 25px;
        z-index: 1;
        cursor: default;
        user-select: none;

        .time {
            margin: auto;
            text-align: center;
            font-size: 50px;
            text-shadow: 0 0 4px black;
            line-height: 1em !important;

            span.time-format {
                font-size: .4em;
                text-shadow: 0 0 4px black;
            }
        }

        .date {
            margin: auto;
            text-align: center;
            font-size: 17px;
            text-shadow: 0 0 3px black;
            line-height: 1em !important;
        }
    }
}

.blurring.dimmed.dimmable>:not(.dimmer) {
    filter: blur(5px) grayscale(.2) !important;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: #ecececcc;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
}