body.dark {

    .ui.modal>.header,
    .ui.modal>.actions {
        background-color: #111;
        color: #fff;
    }

    .ui.modal>.content {
        background-color: #212121;
        color: #fff;
    }

    .btn-favorites,
    .btn-apps {
        color: #000000;
        font-weight: bold;
        text-shadow: 0 0 2px #ffffff;
    }

    .container-options {
        .btn-options {
            color: #000000;
            font-weight: bold;
            text-shadow: 0 0 2px #ffffff;

            .popup-options {
                background-color: #171717;
                border: 1px solid #fff;
                box-shadow: 0 0 7px #929292;

                .tab-option {
                    color: #cccccc;
                    background-color: #171717;

                    .tab {
                        background-color: #212121;
                        box-shadow: 0 0 2px 0px #fff;

                    }

                    #tab-background.tab {

                        #image-selector {
                            background-color: #2b2b2b;

                            >* {
                                .img-background-border {
                                    border: 2px dashed #fff;
                                }

                                label[for="file-to-push"]:hover {
                                    background-color: #151515;
                                }
                            }

                        }

                        .image-options div.options .ui.checkbox label {
                            color: white;
                        }

                    }

                    #tab-favorites.tab {

                        >.accordion {
                            background: #2b2b2b;

                            .title {
                                color: #ffffff9e;
                            }

                            .title.active,
                            .title:hover {
                                color: #efefeff2 !important;
                            }
                        }

                        .ui.secondary.button,
                        .ui.secondary.buttons .button {
                            background-color: #ababab;
                            color: #000;
                        }
                    }

                    #tab-settings.tab {
                        background: #2b2b2b;

                        .ui.card.card-settings {
                            background-color: #151515;

                            >.content {
                                color: #fff;

                                >.right .ui.toggle.checkbox {
                                    label {
                                        color: #fff !important;
                                    }

                                    label:before {
                                        box-shadow: 0 0 2px 1px #000;
                                        background: #f7f7f77a;
                                    }
                                }
                            }
                        }

                        >.ui.blue.card,
                        >.ui.blue.cards>.card,
                        >.ui.cards>.blue.card {
                            box-shadow: 0 0 0 1px #2c2c2c, 0 2px 0 0 #2185d0, 0 1px 3px 0 #2c2c2c;
                        }

                        >.ui.brown.card,
                        >.ui.brown.cards>.card,
                        >.ui.cards>.brown.card {
                            box-shadow: 0 0 0 1px #2c2c2c, 0 2px 0 0 #a5673f, 0 1px 3px 0 #2c2c2c;
                        }

                        >.ui.cards>.violet.card,
                        >.ui.violet.card,
                        >.ui.violet.cards>.card {
                            box-shadow: 0 0 0 1px #2c2c2c, 0 2px 0 0 #6435c9, 0 1px 3px 0 #2c2c2c;
                        }

                        >.ui.cards>.orange.card,
                        >.ui.orange.card,
                        >.ui.orange.cards>.card {
                            box-shadow: 0 0 0 1px #2c2c2c, 0 2px 0 0 #f2711c, 0 1px 3px 0 #2c2c2c;
                        }

                        >.ui.cards>.red.card,
                        >.ui.red.card,
                        >.ui.red.cards>.card {
                            box-shadow: 0 0 0 1px #2c2c2c, 0 2px 0 0 #db2828, 0 1px 3px 0 #2c2c2c;
                        }

                        >.ui.cards>.pink.card,
                        >.ui.pink.card,
                        >.ui.pink.cards>.card {
                            box-shadow: 0 0 0 1px #2c2c2c, 0 2px 0 0 #e03997, 0 1px 3px 0 #2c2c2c;
                        }

                        >.ui.cards>.yellow.card,
                        >.ui.yellow.card,
                        >.ui.yellow.cards>.card {
                            box-shadow: 0 0 0 1px #2c2c2c, 0 2px 0 0 #fbbd08, 0 1px 3px 0 #2c2c2c;
                        }

                        >.ui.cards>.green.card,
                        >.ui.green.card,
                        >.ui.green.cards>.card {
                            box-shadow: 0 0 0 1px #2c2c2c, 0 2px 0 0 #21ba45, 0 1px 3px 0 #2c2c2c;
                        }

                        >.ui.black.card,
                        >.ui.black.cards>.card,
                        >.ui.cards>.black.card {
                            box-shadow: 0 0 0 1px #2c2c2c, 0 2px 0 0 #cfcfcf, 0 1px 3px 0 #2c2c2c;
                        }
                    }
                }

                .tab-option-active {
                    color: #5692F5;
                    background-color: #111;
                }
            }

            .triangle {
                background-color: #171717;
                border: 1px solid #fff;

                .square {
                    background-color: #171717;
                }
            }
        }

        .date-time {
            color: #000000;
            font-weight: bold;

            .time,
            .time span.time-format,
            .date {
                text-shadow: 0 0 2px #ffffff;
            }
        }
    }

    .footer-box .title {
        color: #000;
        font-size: 20px;
        font-weight: bold;
        text-shadow: 0 0 2px #ffffff;
        box-shadow: 0 1px 0px white;
        border-bottom: 2px solid #000;
    }
}