.btn-apps {
    position: absolute;
    top: 32px;
    left: 150px;
    color: white;
    font-size: 23px;
    align-self: center;
    text-shadow: 0 0 3px black;
    z-index: 3;
    width: 55px;
    height: 30px;
    cursor: default;
    user-select: none;
}

.btn-apps.to-left {
    left: 25px;
}

.popup-apps {
    position: relative;
    width: 0px;
    height: 0px;
    border: unset;
    background-color: #000000a8;
    top: 10px;
    overflow: hidden;
    box-shadow: 0 0 3px black;
    color: white;
    font-weight: normal;

    ul {
        width: 199px;
        list-style: none;
        margin: 0;
        padding: 15px 8px;
        cursor: auto;

        a {
            color: white;
            text-decoration: none;

            img {
                vertical-align: middle;
                margin: 0 7px 0 5px;
            }

            li {
                padding: 5px 0;
                font-size: 17px;
            }
        }

        li:hover {
            background-color: #ffffff80;
        }

        div.line-separator {
            border-bottom: 1px solid #fff;
            height: 0px;
            width: 200px;
            margin: 10px 0 10px -15px;
        }
    }
}

@keyframes showApps {
    From {
        width: 0px;
        height: 0px;
        border: 1px solid white;
    }

    To {
        width: 200px;
        height: 465px;
        border: 1px solid white;
    }
}

@keyframes hiddenApps {
    From {
        width: 200px;
        height: 465px;
        border: 1px solid white;
    }

    99% {
        width: 0px;
        height: 0px;
        border: 1px solid white;
    }

    100% {
        border: 0px solid white;
    }

}