#root {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center center !important;
    box-shadow: inset 0px 0px 20px 0px black;
    background-color: black;
}

.last-bg {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    background-position: center center;
    z-index: -1;
}

.col {
    display: flex;
    flex-direction: column;
}

.bold {
    font-weight: bold;
}

.modal-item {
    .msg {
        font-weight: bold;
        color: rgb(185, 31, 31);
        position: absolute;
    }

    label {
        user-select: none;
    }

    div.ui.image {
        margin: auto;
        box-shadow: 0 0 3px 1px black;
    }

    div.ui.image.old-icon-image,
    div.ui.image.new-icon-image {
        width: 80px;
        height: 80px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    div.ui.image+i.angle.double {
        display: flex;
        margin: auto 0;
        justify-content: center;
    }

    .ui.input {
        width: -webkit-fill-available;
        width: -moz-available;
        font-size: 16px;
        margin: 0 0 10px 0;
    }

    button.remove {
        left: 0;
        position: absolute !important;
    }

    .box-file-switch {
        display: flex;
        align-items: center;
        justify-content: space-evenly;


        label.ui.label {
            font-size: 16px;
            cursor: pointer;
        }

        >div {
            font-size: 19px;
        }
    }

    .switches {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        label {
            position: relative;
            display: inline-block;
            width: 31px;
            height: 17px;
            vertical-align: middle;
            cursor: pointer;
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;


            input[value="0"]+span {
                border-radius: 100px;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color: #cccccc;
            }

            input[value="1"]+span {
                border-radius: 100px;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color: #5e72e4;
            }

            input[value="0"]+span+span {
                position: absolute;
                top: 2px;
                bottom: 2px;
                right: 15px;
                left: 2px;
                background-color: #fff;
                border-radius: 9px;
                -webkit-transition: all 100ms ease;
                transition: all 100ms ease;
            }

            input[value="1"]+span+span {
                position: absolute;
                top: 2px;
                bottom: 2px;
                right: 2px;
                left: 15px;
                background-color: #fff;
                border-radius: 9px;
                -webkit-transition: all 100ms ease;
                transition: all 100ms ease;
            }
        }
    }
}

.modal-group {

    .msg {
        font-weight: bold;
        color: rgb(185, 31, 31);
    }

    .ui.input {
        width: -webkit-fill-available;
        width: -moz-available;
        font-size: 16px;
        margin: 0 0 10px 0;
    }

    button.remove {
        left: 0;
        position: absolute !important;
    }
}