.footer-box {
     position: absolute;
     bottom: 0;
     left: 0;
     right: 0;
     z-index: 1;
     display: flex;
     justify-content: center;
     flex-flow: row;
     height: 120px;

     .title {
          width: 95%;
          text-align: center;
          align-self: center;
          color: #fff;
          font-size: 19px;
          font-weight: bold;
          margin: 0;
          text-shadow: 0 0 3px black;
          border-bottom: 2px solid #fff;
          box-shadow: 0 1px 0px #000;
     }

     .icons-box {
          display: flex;
          margin: auto;

          .link-image-icon {
               margin: auto;
               padding: 0 6px;

               img.image-icon {
                    width: auto;
                    height: 50px;
                    opacity: 0.5;
                    transition: 0.3s;
               }
          }
     }
}