.btn-favorites {
    position: absolute;
    top: 32px;
    left: 25px;
    color: white;
    font-size: 23px;
    align-self: center;
    text-shadow: 0 0 3px black;
    cursor: default;
    z-index: 3;
    width: 110px;
    height: 30px;
}

.popup-favorites {
    position: relative;
    width: 0px;
    height: auto;
    border: unset;
    background-color: #000000a8;
    top: 10px;
    overflow: hidden;
    box-shadow: 0 0 3px black;

    ul.group {
        width: 197px;
        list-style: none;
        margin: 0;
        padding: 15px 8px;
        cursor: default;
        max-height: 600px;
        overflow: auto;
        color: white;
        font-weight: normal;

        li.group-name {
            padding: 5px 0;
            font-size: 19px;
            text-align: center;
            border-bottom: 1px solid #fff;
            user-select: none;
        }

        ul.item {
            list-style: none;
            margin: 0;
            padding: 8px;
            cursor: default;

            a {
                color: white;
                text-decoration: none;

                img {
                    vertical-align: middle;
                    margin: 0 7px 0 5px;
                }

                li {
                    padding: 5px 0;
                    font-size: 17px;
                }
                
                li:hover {
                    background-color: #ffffff80;
                }
            }
        }

        div.line-separator {
            border-bottom: 1px solid #fff;
            height: 0px;
            width: 200px;
            margin: 10px 0 10px -15px;
        }
    }
}

@keyframes showFavoritesApps {
    From {
        width: 0px;
        border: 1px solid white;
    }

    To {
        width: 200px;
        border: 1px solid white;
    }
}

@keyframes hiddenFavoritesApps {
    From {
        width: 200px;
        border: 1px solid white;
    }

    99% {
        width: 0px;
        border: 1px solid white;
    }

    100% {
        border: 0px solid white;
    }

}